import { AbstractFilter } from './abstract_filter'

export class Range extends AbstractFilter {

    private field: string;
    private data: any = {};

    constructor(field: string) {
        super();
        this.field = field;
    }

    public static make(field: string): Range {
        return new Range(field);
    }

    public gte(value: any): this {
        return this.set('gte', value);
    }

    public gt(value: any): this {
        return this.set('gt', value);
    }

    public lte(value: any): this {
        return this.set('lte', value);
    }

    public lt(value: any): this {
        return this.set('lt', value);
    }

    public boost(value: number = 1): this {
        return this.set('boost', value);
    }

    public format(value: string): this {
        return this.set('format', value);
    }

    public timezone(value: string) {
        return this.set('time_zone', value);
    }

    private set(option: string, value: any): this {
        this.data[option] = value;
        return this;
    }

    toJSON() {

        let obj: any = { range: {} };
        obj.range[this.field] = this.data;

        return obj;
    }
}